*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body {
  width:100%;
  margin: 0;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Header */
.Header{
   background-color: rgb(89, 182, 253);
   height:100px;
   display: flex;
   justify-content: space-around;
   align-items: center;
}

.Nav  .list-Container li{
  list-style: none;
  display: inline-block;
  padding: 20px;
}
.Nav .list-Container li:hover, .Nav .list-Container li:focus{
 background-color: rgb(15, 3, 50);
 color: aliceblue;
 cursor: pointer; 
}
/* Search Bar design */
.searchContainer{
  height:100px;
  background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.searchContainer form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.searchContainer form input{
  padding: 30px;
}
/* Feed Section */
.Feed{
   height:200px;
   display: grid; 
   gap: 20px;
   justify-content: center;
   align-items: center;
   overflow-y: scroll;
}

.Feed .Post{
  border: 2px solid black;
}

/* Addpost */
.Addpost{
  width: 100%;
  background-color: rgb(255, 234, 127);
  display: flex;
  justify-content: center;
  align-items: center;
}

.Addpost form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Addpost form #title{
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  outline:none;
}

.Addpost form #body{
  padding: 30px;
  border-radius: 5px;
}

.Addpost form button{
  margin-top: 10px;
  padding: 10px;
  width: 60%;
  border-radius: 5px;
  background-color: rgb(15, 3, 50);
  color: aliceblue;
  cursor: pointer; 
}

/* Footer */

.Footer{
  width: 100%;
  height:80px;
  background-color: rgb(43, 160, 250);
  position: fixed;
  left:0;
  bottom:0;
  display: flex;
  justify-content: center;
  align-items: center;
}
